import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


export default class Homeslider extends Component {
  render() {
    const settings = {
      dots: false,
      arrows: false,
      fade: true,
      infinite: true,
      autoplay: true,
      speed: 500,
      lazyLoad: true,
      autoplaySpeed: 3000,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
        <div className="banners">
            <Slider {...settings}>
                <div>
                    <img loading="lazy" src="/img/hero-bg.jpg" alt="banner 1" />
                </div>
                <div>
                    <img loading="lazy" src="/img/inter-bg.jpg" alt="banner 2" />
                </div>
                <div>
                    <img loading="lazy" src="/img/inter-bg2.jpg" alt="banner 3" />
                </div>
            </Slider>
        </div>
    );
  }
}